import React, { useEffect, useState } from 'react';
import { TonClient } from '@tonclient/core';
import TonConnect from '@tonconnect/sdk';
import { TonConnectUI } from '@tonconnect/ui';

function App() {
    const [connected, setConnected] = useState(false);

    useEffect(() => {
        const initClient = async () => {
            try {
                new TonClient({
                    network: { endpoints: ["https://main.ton.dev"] }
                });
            } catch (error) {
                console.error("Failed to initialize TON Client", error);
            }
        };

        initClient();
    }, []);

    const handleConnect = async () => {
        try {
            const tonConnect = new TonConnect({
                manifestUrl: 'https://liomlong.github.io/pigminiapp/tonconnect-manifest.json',
            });
            const ui = new TonConnectUI(tonConnect);
            await ui.connectWallet();
            setConnected(true);
            console.log('Connected to TON Wallet');
        } catch (error) {
            console.error("Failed to connect to TON Wallet", error);
        }
    };

    return (
        <div className="App">
            <h1>Pig Mini App - Domain Sales</h1>
            <button onClick={handleConnect}>Connect to TON Wallet</button>
            {connected && <p>Connected to TON Wallet</p>}
        </div>
    );
}

export default App;
